@import './normalize.css';
@import './vars';
@import './mixins';
@import './styles';
@import url('https://fonts.googleapis.com/css2?family=Audiowide&display=swap');
@import url('https://at.alicdn.com/t/font_1837475_qrpqd86lvt.css');

// global
html { font-family: $font-family-base; }
h3 { color: lighten(black, 20%); }
p {margin: 0;line-height: 1.4rem;color: gray;}
a { display: flex; align-items: center;color: $c_primary;font-size: 1.2rem; }

// IDs
#header {@include shadow-v(1px);font-family: $font-family-h;}
#footer { 
  @include shadow-v(-1px);
  display: flex;
  align-items: center;
}

#me-container {
  display: grid;
  grid-template-columns: 1fr 2fr;
}
@media(max-width:'501px') {
  #me-container {
    grid-template-columns: 1fr;
  }
}
