@use './vars';

@mixin shadow-v($y-offset) { box-shadow: 0 $y-offset 4px 0 lightgray; }

@mixin theme($front,$back) { color: $front; background-color: $back; }
@mixin theme_error {@include theme(white, lighten(red,10%));}
.theme-error{@include theme_error();}
@mixin theme_primary {@include theme(white, $c_primary);}
.theme-primary{@include theme_primary();}
@mixin theme_primary_dark {@include theme(white, darken($c_primary,10%));}
.theme-primary{@include theme_primary_dark();}