
.container-center { display: flex; justify-content: center; align-items: center; }

%btn-shared {
  @include theme_primary_dark();
  padding: 0.5rem;
  border-radius: 0.5rem;
  transition: all 0.2s ease-in-out;
  font-family: $font-family-h;
  font-size: 0.8rem;
  &:hover {
    background-color: $c_primary;
    transform: translateX(1px);
  }
}
.btn-primary { @extend %btn-shared; }
.link-w-img-round { @extend %btn-shared; padding: 4px; border-radius: 1.1rem; }

.error-1 {
  @include theme_error();
  border-radius: 0.2rem;
  padding: 0.2rem;
}

.spinner {
  border: 0.4rem solid #f3f3f3;
  border-top: 0.4rem solid $c_primary;
  margin: 2rem auto;
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  animation: spin 2s linear infinite;
}
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}



// - - - UTILS - - -

.p1 {padding: 0.5rem;}
.p2 {padding: 1rem;}
.m1 {margin: 0.5rem;}
.m2 {margin: 1rem;}
.myb1 {margin-bottom: 0.5rem;}
.myb2 {margin-bottom: 1rem;}
.mx1 {margin-left: 0.5rem;margin-right: 0.5rem;}



